html {
  overflow-y: scroll;
  padding:0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-stretch: expanded;
  font-size: 12px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    white-space: pre;
  color: blue;
}
h1 {
  font-size: 18px;
  vertical-align: bottom;
  margin: 0;
}


h2 {
  font-size: 18px;
}

h3 {
  font-size: 14px;
}

p {
  font-size: 12px;
  margin: 0;
}



a{
  text-decoration: none;
  color: inherit;
}

a:hover{
  color: grey;
}











/* menu */

.nav ul {
  list-style: none;
  padding: 0;
}

.nav-title a{
  color: inherit;
  text-decoration: none; 
 
}

.nav-item a{
  color: inherit;
  text-decoration: none; 
}

.nav-item .link:hover {
  color: grey;
}

.nav-item .link:focus {
  color: rgb(218, 0, 0);
}

.nav-item .link-active {
  color: rgb(218, 0, 0);
}

.nav-item .link-active:hover {
  color: rgb(147, 0, 0);
}
.proj-title{

margin: 0;

}

.proj-details{
  
  color: rgb(100, 100, 100);
}


.proj-desc{
  white-space: pre;
  text-wrap: balance;
  
}

.proj-media-text p{
  white-space: pre;
  text-wrap: balance;
  
}

.proj-desc a:hover{
 color: rgb(218, 0, 0);
  
}

.whitespace{
  white-space: pre-line;
}

