/* menu */

.nav ul {
  list-style: none;
  padding: 0;
}

.nav-title a{
  color: inherit;
  text-decoration: none; 
 
}

.nav-item a{
  color: inherit;
  text-decoration: none; 
}

.nav-item .link:hover {
  color: grey;
}

.nav-item .link:focus {
  color: rgb(218, 0, 0);
}

.nav-item .link-active {
  color: rgb(218, 0, 0);
}

.nav-item .link-active:hover {
  color: rgb(147, 0, 0);
}